.hero__section {
    padding-top: 60px;
    padding-bottom: 40px;
    margin-left: auto;
    margin-right: auto;
}

.hero__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
}

.hero__text {
    font-family: Poppins;
    margin-top: 0;
    font-weight: 600;
    font-size: 22px;
    line-height: 1.9;
    color: #fd5103;
    margin-bottom: 60px;
}

.select__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.select__text-mob {
    margin-top: 0;
    margin-bottom: 20px;
    text-align: center;
    font-family: Poppins;
    font-weight: 500;
    font-size: 14px;
    line-height: 2;
    letter-spacing: 0.02em;
    color: #000000;
    opacity: 0.8;
}

.select__button {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 93px;
    height: 34px;
    color: #fd5103;
    border: 1px solid #fd5103;
    border-radius: 4px;
    padding-top: 5px;
    padding-right: 8px;
    padding-bottom: 5px;
    padding-left: 10px;
    font-family: Poppins;
}

.select__icon {
    fill: #fd5103;
}

.select__button:hover {
    color: #fcfcfc;
    background-color: #fd5103;
}

.select__icon:hover {
    fill: #fcfcfc;
}

.select__letter {
    font-family: Poppins;
    font-weight: 700;
    font-size: 16px;
    line-height: 13.5;
}

.select__list {
    display: none;
    box-sizing: border-box;
    overflow: auto;
    list-style: none;
    margin: 0;
    padding-left: 0;
    width: 93px;
    height: 84px;
    border: 1px solid #fd5103;
    border-radius: 4px;
}

.select__item {
    font-family: Poppins;
    font-weight: 700;
    font-size: 10px;
    line-height: 15px;
    padding-left: 10px;
    padding-top: 6px;
    padding-bottom: 6px;

    font-weight: 500;
    font-size: 14px;
    line-height: 2;
    letter-spacing: 0.02em;

    color: #5f6775;
}

.select__list-open {
    display: block;
}

.alphabet__button {
    border: none;
    background-color: transparent;
    width: 24px;
    height: 25px;
    padding: 0;
    cursor: pointer;
    font-family: Poppins;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5;
    // font-family: inherit;

    color: #5f6775;

    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        scale 250ms cubic-bezier(0.4, 0, 0.2, 1),
        text-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.alphabet__button:hover,
.alphabet__button:focus {
    color: #fd5103;
    scale: 1.2;
    text-shadow: 0px 4px 4px rgba(95, 103, 117, 0.25);
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: #5f6775;
}

.alphabet__text {
    display: block;
    margin-bottom: 40px;
    margin-top: 0;
    color: #202025;
    opacity: 0.8;
    font-family: Poppins;
    font-weight: 500;
    font-size: 14px;
    line-height: 2;
}

.alphabet__list {
    width: 324px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

@media screen and (min-width: 480px) {
    .hero__section {
        width: 480px;
    }
}

@include mobileMax {
    .alphabet__wrapper {
        display: none;
    }

    .hero__image {
        margin-bottom: 60px;
    }
}

@media screen and (min-width: 768px) {
    .hero__section {
        width: 768px;
        padding-top: 77px;
        padding-bottom: 120px;
    }

    .select__wrapper {
        display: none;
    }

    .hero__container {
        flex-direction: row;
        justify-content: space-between;
        padding-left: 32px;
        padding-right: 32px;
    }

    .hero__text {
        width: 336px;
        font-weight: 600;
        font-size: 26px;
        line-height: 1.62;

        margin-bottom: 40px;
    }
}

@include desktop {
    .hero__section {
        width: 1280px;
        padding-top: 89px;
        padding-bottom: 120px;
    }

    .hero__container {
        margin-left: 16px;
        margin-right: 16px;
    }

    .hero__text {
        width: 688px;
        font-size: 52px;
        line-height: 1.54;

        margin-bottom: 40px;
    }

    .alphabet__text {
        font-size: 18px;
        line-height: 1.56;
    }

    .alphabet__button {
        width: 40px;
        height: 40px;

        font-size: 24px;
        line-height: 1.5;
    }

    .alphabet__list {
        width: 520px;
    }
}